.science-feature{
    overflow: hidden;
    position: relative;
    .section-title{
        margin-bottom: 120px;
        width: 60%;
        @include desktop-lg{
            width: 100%;
        }
        @include mobile{
            margin-bottom: 90px;
        }
    }
    &-item{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 65px;
        align-items: center;
        margin: 0 100px;
        @include desktop{
            grid-template-columns: repeat(1, 1fr);
            row-gap: 50px;
        }
        @include mobile{
            row-gap: 30px;
        }
        @include desktop-lg{
            margin: 0;
        }
        &:nth-child(odd){
            .science-feature-item-image{
                order: 2;
                @include desktop{
                    order: 0;
                }
            }
        }
        &:not(:last-child){
            margin-bottom: 150px;
            @include mobile{
                margin-bottom: 90px;
            }
        }
        &-image{
            width: 100%;
            position: relative;
            z-index: 2;
            img{
                height: 100%;
                width: 100%;
                border-radius: 18px;
            }
            &::before{
                position: absolute;
                content: '';
                height: 100px;
                width: 90%;
                border-radius: 20px;
                top: -30px;
                left: 50%;
                transform: translateX(-50%);
                background: $primary-color;
                z-index: -1;
                @include mobile{
                    width: 80%;
                    top: -20px;
                }
            }
        }
        &-content{
            @include desktop{
                text-align: center;
            }
            p{
                &:not(:last-child){
                    margin-bottom: 35px;
                    @include mobile{
                        margin-bottom: 20px;
                    }
                }
            }
            a{
                margin-top: 60px;
                @include desktop{
                    margin-top: 0;
                }
            }
        }
    }
    #particles-gray-4 {
        position: absolute;
        opacity: 0.15;
        width: 790px;
        height: 790px;
        top: 8%;
        right: -20%;
        transform: rotate(105deg);
        @include mobile{
            opacity: .075;
        }
    }
}