.contact{
    min-height: 600px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    border-radius: 0 0 50px 50px;
    padding: 200px 0;
    @include desktop-lg{
        min-height: 400px;
    }
    @include desktop{
        border-radius: 0 0 10px 10px;
    }
    @include mobile-xs{
        padding: 150px 0 100px 0;
    }
    &-banner{
        text-align: center;
        h2{
            color: $white;
        }
    }
}
.contact-form{
    padding: 0 0 150px 0;
    @include desktop{
        padding: 0 0 100px 0;
    }
    @include tablet{
        padding: 0 0 50px 0;
    }
    &-wrapper{
        width: 60%;
        margin: 0 auto;
        margin-top: -200px;
        position: relative;
        z-index: 1;
        @include desktop-lg{
            width: 80%;
            margin-top: -150px;
        }
        @include desktop{
            width: 100%;
        }
        .form{
            padding: 100px;
            background: $white;
            box-shadow: 0 20px 40px rgba($color: #000000, $alpha: 0.1);
            border-radius: 20px;
            @include tablet{
                padding: 50px;
            }
            @include mobile-xs{
                padding: 20px;
                border-radius: 10px;
            }
            &-field{
                width: 100%;
                height: 60px;
                border-radius: 5px;
                overflow: hidden;
                border: 1px solid $primary-color;
                box-shadow: none;
                padding: 10px 20px;
                &:not(:last-child){
                    margin-bottom: 20px;
                }
                input{
                    height: 100%;
                    width: 100%;
                    border: none;
                    font-size: 16px;
                    font-family: $primary-font;
                    &:focus{
                        outline: 0;
                    }
                    &::placeholder{
                        color: rgba($color: #000000, $alpha: 0.3);
                    }
                }
                &.message{
                    height: 150px;
                    textarea{
                        height: 100%;
                        width: 100%;
                        border: none;
                        font-size: 16px;
                        font-family: $primary-font;
                        &:focus{
                            outline: 0;
                        }
                        &::placeholder{
                            color: rgba($color: #000000, $alpha: 0.3);
                        }
                    }
                }
            }
            .button{
                border: none;
                font-family: $primary-font;
                @include mobile{
                    display: block;
                    width: 100%;
                }
                &:focus{
                    outline: 0;
                }
            }
        }
    }
}