.science-service{
    padding: 150px 0;
    overflow: hidden;
    position: relative;
    @include tablet{
        padding: 100px 0;
    }
    @include mobile{
        padding: 50px 0;
    }
    &-wrapper{
        margin: 0 100px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 50px;
        @include desktop-lg{
            margin: 0;
        }
        @include desktop{
            grid-template-columns: repeat(1, 1fr);
            row-gap: 50px;
        }
        &-item{
            padding: 40px 55px 60px 50px;
            background: $white;
            box-shadow: 0 20px 40px rgba($color: #000000, $alpha: 0.1);
            border-radius: 20px;
            text-align: center;
            position: relative;
            @include mobile{
                padding: 30px;
            }
            &::before{
                content: '';
                position: absolute;
                height: 2px;
                width: 85%;
                background: #eecb6b;
                top: 0;
                left: 50%;
                -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
            }
            h4{
                font-size: 30px;
                line-height: 40px;
                margin: 0 0 50px 0;
                position: relative;
                &::before{
                    position: absolute;
                    content: "";
                    width: 100px;
                    height: 3px;
                    background: $text-color-dark;
                    left: 50%;
                    bottom: -18px;
                    transform: translateX(-50%);
                }
            }
            p{
                font-size: 20px;
                line-height: 31px;
                @include mobile{
                    font-size: 16px;
                    line-height: 26px;
                }
                &:not(:last-child){
                    margin-bottom: 35px;
                    @include mobile{
                        margin-bottom: 20px;
                    }
                }
            }
        }
    }
    #particles-gray-5 {
        position: absolute;
        opacity: 0.15;
        width: 700px;
        height: 700px;
        bottom: -18%;
        right: -20%;
        transform: rotate(105deg);
        z-index: -1;
        @include mobile{
            opacity: .075;
        }
    }
}