.cta {
  padding: 137px 0 150px 0;
  @include desktop{
    overflow: hidden;
  }
  @include tablet{
    padding: 50px 0;
  }
  &-wrapper {
    background: $text-color-dark;
    padding: 50px 100px;
    border-radius: 20px;
    display: grid;
    grid-template-columns: 300px auto;
    align-items: center;
    column-gap: 46px;
    position: relative;
    @include desktop-lg{
      padding: 50px;
    }
    @include tablet{
      padding: 30px;
    }
    @include desktop{
      grid-template-columns: auto;
      row-gap: 30px;
      text-align: center;
      justify-items: center;
    }
    &-image {
      height: 300px;
      width: 300px;
      z-index: 1;
      @include desktop{
        height: 250px;
        width: 250px;
      }
      @include tablet{
        height: 200px;
        width: 200px;
      }
      img {
        height: 100%;
        width: 100%;
      }
    }
    &-content {
      h3 {
        color: $white;
        line-height: 53px;
        margin: 0 0 26px 0;
        @include tablet{
          font-size: 30px;
          line-height: 40px;
        }
      }
    }
    #particles-gray {
      position: absolute;
      width: 796px;
      height: 796px;
      opacity: 0.1;
      transform: rotate(60deg) translateX(-50%);
      left: 0;
      top: 60%;
    }
  }
}
