.science-study{
    padding: 0 0 150px 0;
    @include tablet{
        padding: 0 0 100px 0;
    }
    @include mobile{
        padding: 0 0 50px 0;
    }
    .section-title{
        @include desktop-lg{
            width: 100%;
        }
    }
    &-wrapper{
        width: 70%;
        margin: 0 auto;
        @include desktop-lg{
            width: 100%;
        }
        table{
            width: 100%;
            border: 1px solid #E0E2E9;
            text-align: left;
            display: table;
            border-spacing: 0;
            border-radius: 20px;
            overflow: hidden;
            @include mobile{
                border-radius: 10px;
            }
            td, th {
                padding: 25px 30px;
                @include mobile{
                    padding: 10px;
                }
            }
            th{
                background: $text-color-dark;
                color: $white;
                &:not(:last-child){
                    border-right: 1px solid rgba($color: #E0E2E9, $alpha: .3);
                }
                &:not(:first-child){
                    text-align: center;
                }
                &:nth-child(1){
                    width: 60%;
                    @include mobile{
                        width: 50%;
                    }
                }
                &:nth-child(2){
                    width: 17%;
                    @include mobile{
                        width: 20%;
                    }
                }
                &:nth-child(3){
                    width: 23%;
                    @include mobile{
                        width: 30%;
                    }
                }
            }
            td{
                border-bottom: 1px solid rgba($color: #E0E2E9, $alpha: 1);
                background: $white;
                @include mobile{
                    font-size: 15px;
                    line-height: 26px;
                }
                &:not(:last-child){
                    border-right: 1px solid rgba($color: #E0E2E9, $alpha: 1);
                }
                &:not(:first-child){
                    text-align: center;
                }
                a{
                    padding: 3px 15px;
                    background: #EE5353;
                    border-radius: 4px;
                    text-decoration: none;
                    color: $white;
                    display: inline-block;
                    font-size: 15px;
                    transition: all .3s ease;
                    &:hover{
                        background: #F63939;
                    }
                }
            }
            tr{
                &:last-child{
                    td{
                        border-bottom: none;
                    }
                }
            }
        }
    }
    
}