.project-product{
    padding: 0 0 150px 0;
    position: relative;
    overflow: hidden;
    text-align: center;
    @include tablet{
        padding: 0 0 50px 0;
    }
    &-wrapper{
        margin: 0 100px 100px 100px;
        border-radius: 10px;
        overflow: hidden;
        background: $white;
        position: relative;
        z-index: 1;
        @include desktop-xl{
            margin: 0 0 100px 0;
        }
        @include tablet{
            margin: 0 0 50px 0;
        }
        .product-heading{
            display: flex;
            align-items: center;
            text-align: center;
            .product,.consumtion,.consumer,.effects{
                padding: 27px 0;
                background: $text-color-dark;
                color: $white;
                flex: 1;
                font-size: 13px;
                &:not(:last-child){
                    border-right: 1px solid rgba($color: #E0E2E9, $alpha: .3);
                }
            }
            .product {
                @include desktop-lg{
                    min-width: 286px;
                }
                @include desktop{
                    min-width: 200px;
                }
                @include mobile{
                    width: 50%;
                    min-width: unset;
                }
            }
            .consumer{
                @include tablet{
                    display: none;
                }
            }
            .consumtion{
                @include mobile{
                    display: none;
                }
            }
        }
        .product-body{
            border-left: 1px solid #E0E2E9;
            border-right: 1px solid #E0E2E9;
            border-bottom: 1px solid #E0E2E9;
            border-radius: 0 0 10px 10px;
            .project-product-wrapper-item{
                display: flex;
                &:not(:last-child){
                    border-bottom: 1px solid #E0E2E9;
                }
                .consumtion,.consumer,.effects{
                    display: flex;
                    align-items: center;
                    flex: 1;
                    justify-content: center;
                    span{
                        font-size: 18px;
                        @include desktop-xl{
                            font-size: 15px;
                        }
                    }
                }
                .product{
                    min-width: 290px;
                    border-right: 1px solid #E0E2E9;
                    text-align: center;
                    padding: 35px 0;
                    @include desktop-xl{
                        min-width: 284px;
                    }
                    @include desktop{
                        min-width: 200px;
                    }
                    @include mobile{
                        width: 50%;
                        min-width: unset;
                    }
                    img{
                        height: 200px;
                        width: 200px;
                        border: 1px solid #E0E2E9;
                        @include desktop{
                            height: 150px;
                            width: 150px;
                        }
                        @include desktop-xl{
                            height: 100px;
                            width: 100px;
                        }
                    }
                    span{
                        display: block;
                        font-size: 16px;
                        line-height: 24px;
                        margin-top: 15px;
                        @include desktop-xl{
                            font-size: 14px;
                        }
                    }
                }
                .consumtion{
                    border-right: 1px solid #E0E2E9;
                    @include mobile{
                        display: none;
                    }
                }
                .consumer{
                    border-right: 1px solid #E0E2E9;
                    @include tablet{
                        display: none;
                    }
                }
                .effects{
                    ul{
                        margin: 0;
                        padding: 0;
                        text-align: left;
                        li{
                            list-style: none;
                            span{
                                display: inline-block;
                                &:nth-child(1){
                                    min-width: 100px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    #particles-gray-7 {
        position: absolute;
        opacity: 0.15;
        width: 700px;
        height: 700px;
        left: -14%;
        top: 10%;
        transform: rotate(105deg);
        @include mobile {
            opacity: .075;
        }
    }
}