.science-banner {
  min-height: 750px;
  display: flex;
  align-items: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  border-radius: 0 0 50px 50px;
  padding: 220px 0 130px;
  position: relative;
  @include desktop{
    padding: 160px 0 100px;
    border-radius: 0 0 20px 20px;
  }
  @include tablet{
    padding: 130px 0 100px;
  }
  &-wrapper {
    display: grid;
    grid-template-columns: 45% 55%;
    grid-column-gap: 30px;
    align-items: center;
    margin: 0 50px 0 100px;
    @include desktop-lg{
      margin: 0;
    }
    @include tablet{
      grid-template-columns: 100%;
    }
    &-content {
      @include tablet{
        margin-bottom: 60px;
        text-align: center;
      }
      h1,
      p {
        color: $white;
      }
      h1 {
        font-size: 70px;
        margin-bottom: 30px;
        line-height: 80px;
        @include desktop{
          font-size: 50px;
          line-height: 60px;
        }
        @include tablet{
          font-size: 40px;
          line-height: 50px;
        }
      }
      p {
        line-height: 30px;
        @include tablet{
          font-size: 16px;
          line-height: 26px;
        }
        &:not(:last-child) {
          margin-bottom: 35px;
        }
        a {
          color: $white;
        }
      }
    }
    &-chart {
      margin: 0 0 -200px 0;
      border-radius: 20px;
      position: relative;
      z-index: 2;
      box-shadow: 0 20px 40px rgba($color: #000000, $alpha: 0.1);
      padding: 40px 30px 35px 25px;
      text-align: center;
      @include desktop-lg{
        margin: 0;
      }
      &::before {
        content: "";
        position: absolute;
        height: 100%;
        width: 100%;
        border-radius: 20px;
        background-color: $white;
        top: 0;
        left: 0;
        z-index: -1;
      }
      &::after {
        position: absolute;
        content: "";
        height: 100px;
        width: 90%;
        border-radius: 20px;
        top: -30px;
        left: 50%;
        transform: translateX(-50%);
        background: $primary-color;
        z-index: -2;
        animation-name: up-down-animation;
        animation-duration: 3s;
        animation-iteration-count: infinite;
        animation-direction: alternate;
        @include mobile{
          width: 80%;
          top: -20px;
        }
      }
      img {
        margin-bottom: 37px;
        width: 100%;
      }
      &-content {
        display: flex;
        align-items: center;
        justify-content: center;
        @include mobile{
          display: block;
        }
        span.duration {
          font-size: 13px;
          margin-right: 35px;
          @include mobile{
            margin-right: 0;
            margin-bottom: 20px;
            display: block;
          }
        }
      }
    }
  }
  .science-triangles{
      width: 50%;
      height: 100%;
      position: absolute;
      left: 0;
      top: -10%;
      @include mobile{
        width: 90%;
        right: 10%;
      }
      img {
        transform-origin: center center;
        &:nth-child(1) {
          position: absolute;
          top: 30%;
          left: 27%;
          animation-name: opacity-animation;
          animation-duration: 2.5s;
          animation-iteration-count: infinite;
        }
        &:nth-child(2) {
          position: absolute;
          bottom: 3%;
          left: 10%;
          transform: rotate(10deg);
          animation-name: opacity-animation;
          animation-duration: 2s;
          animation-delay: -0.5s;
          animation-iteration-count: infinite;
        }
      }
  }
}
