.science-test{
    padding-top: 160px;
    position: relative;
    // overflow: hidden;
    @include tablet{
        padding-top: 100px;
    }
    @include mobile{
        padding-top: 50px;
    }
    &-content{
        text-align: center;
        width: 70%;
        margin: 0 auto;
        @include desktop-lg{
            width: 100%;
        }
        h2{
            position: relative;
            margin: 0 0 60px 0;
            @include mobile{
                margin: 0 0 40px 0;
            }
            &::before{
                position: absolute;
                content: "";
                width: 100px;
                height: 3px;
                background: $text-color-dark;
                left: 50%;
                bottom: -18px;
                transform: translateX(-50%);
            }
        }
        ul{
            margin: 0;
            padding: 0;
            @include mobile{
                text-align: left;
                padding: 0 35px;
            }
            li{
                font-size: 20px;
                line-height: 31px;
                list-style: none;
                position: relative;
                @include mobile{
                    font-size: 16px;
                    line-height: 26px;
                }
                &::before{
                    position: absolute;
                    content: '';
                    height: 5px;
                    width: 5px;
                    background: $text-color-dark;
                    top: 50%;
                    transform: translateY(-50%) translateX(-10px);
                    @include mobile{
                        top: 14px;
                        transform: translateY(-50%) translateX(-10px);
                    }
                }
            }
        }
        a{
            font-size: 20px;
            font-weight: bold;
            margin: 35px 0;
            display: inline-block;
            @include mobile{
                font-size: 16px;
                margin: 20px 0;
            }
        }
    }
    #particles-gray-2 {
        position: absolute;
        opacity: 0.1;
        width: 550px;
        height: 550px;
        right: 8%;
        top: 39%;
        transform: rotate(105deg);
        @include tablet{
            display: none;
        }
    }
}