.science-result{
    padding: 150px 0;
    position: relative;
    @include tablet{
        padding: 100px 0;
    }
    @include mobile{
        padding: 50px 0;
    }
    &-wrapper{
        position: relative;
        &-image{
            max-height: 650px;
            height: 100%;
            @include desktop{
                margin-bottom: 30px;
            }
            img{
                border-radius: 20px;
                @include desktop{
                    width: 100%;
                }
            }
        }
        &-content{
            padding: 55px 140px 65px 50px;
            background: $white;
            border-radius: 20px;
            box-shadow: 0 20px 40px rgba($color: #000000, $alpha: 0.1);
            position: absolute;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
            width: 63%;
            @include desktop-lg{
                padding: 55px 50px 60px 50px;
            }
            @include mobile{
                padding: 30px;
            }
            @include desktop{
                position: relative;
                top: unset;
                right: unset;
                transform: unset;
                width: 100%;
                text-align: center;
            }
            &::before {
                position: absolute;
                content: "";
                width: 90%;
                height: 2px;
                background: $primary-color;
                left: 50%;
                top: 0;
                transform: translateX(-50%);
            }
            h3{
                font-size: 30px;
                line-height: 40px;
                margin: 0 0 45px 0;
                position: relative;
                &::before {
                    position: absolute;
                    content: "";
                    width: 100px;
                    height: 3px;
                    background: $text-color-dark;
                    left: 50px;
                    bottom: -15px;
                    transform: translateX(-50%);
                    @include desktop{
                        left: 50%;
                        transform: translateX(-50%);
                    }
                }
            }
            p{
                line-height: 31px;
                &:not(:last-child){
                    margin-bottom: 35px;
                    @include mobile{
                        margin-bottom: 20px;
                    }
                }
            }
            a{
                font-size: 20px;
                font-weight: bold;
                display: inline-block;
                @include mobile{
                    font-size: 16px;
                }
            }
        }
    }
    #particles-gray-3 {
        position: absolute;
        opacity: 0.1;
        width: 550px;
        height: 550px;
        bottom: -33%;
        z-index: -1;
        transform: rotate(105deg);
        @include desktop{
            display: none;
        }
    }
}