.research{
    text-align: center;
    @include mobile{
        overflow: hidden;
    }
    .section-title{
        width: 85%;
        @include desktop{
            width: 100%;
        }
        h2{
            &::before{
                display: none;
            }
        }
    }
    .research-wrapper{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        grid-gap: 60px 40px;
        margin-bottom: 100px;
        @include desktop{
            grid-template-columns: repeat(1, 1fr);
        }
        @include mobile{
            margin-bottom: 50px;
        }
        .research-item{
            &-image{
                border-radius: 20px;
                overflow: hidden;
                display: flex;
                img{
                    height: 100%;
                    width: 100%;
                }
            }
            &-content{
                margin: -115px 85px 0 70px;
                background: $white;
                border-radius: 20px;
                text-align: center;
                padding: 40px 68px;
                box-shadow: 0 20px 40px rgba(0, 0, 0, .1);
                position: relative;
                min-height: 276px;
                align-items: center;
                @include desktop-lg{
                    margin: -115px 30px 0 30px;
                    padding: 30px;
                }
                @include desktop{
                    min-height: auto;
                }
                &::before{
                    content: '';
                    position: absolute;
                    height: 2px;
                    width: 85%;
                    background: $primary-color;
                    top: 0;
                    left: 50%;
                    transform: translateX(-50%);
                }
                h3{
                    font-size: 30px;
                    margin: 0 0 48px 0;
                    position: relative;
                    line-height: 40px;
                    &::before{
                        position: absolute;
                        content: '';
                        height: 3px;
                        width: 100px;
                        left: 50%;
                        bottom: -22px;
                        transform: translateX(-50%);
                        background: $text-color-dark;
                    }
                    @include mobile{
                        font-size: 20px;
                        line-height: 28px;
                    }
                }
                p{
                    margin: 0;
                }
            }
        }
    }
}