.about-client{
    padding: 180px 0 100px;
    @include desktop {
        padding: 100px 0;
    }
    @include tablet {
        padding: 50px 0;
    }
    .section-title{
        @include tablet{
            width: 100%;
        }
        h2{
            font-size: 30px;
            line-height: 60px;
            &::before{
                bottom: -14px;
            }
        }
    }
    &-wrapper{
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        align-items: center;
        column-gap: 30px;
        text-align: center;
        margin: 0 100px;
        @include desktop{
            margin: 0;
        }
        @include desktop{
            grid-template-columns: repeat(2, 1fr);
            row-gap: 30px;
        }
        @include tablet{
            grid-template-columns: repeat(1, 1fr);
        }
        &-item{
            // min-height: 150px;
            border: 1px solid #E0E2E9;
            border-radius: 20px;
            align-self: center;
            min-height: 150px;
            align-items: center;
            display: flex;
            justify-content: center;
        }
    }
}