.project-benefit{
    padding: 150px 0 130px 0;
    overflow: hidden;
    position: relative;
    @include desktop{
        padding: 100px 0;
    }
    @include tablet{
        padding: 50px 0;
    }
    .section-title{
        @include desktop-lg{
            width: 100%;
        }
        h2{
            margin-bottom: 60px;
            line-height: 70px;
            &::before{
                bottom: -15px;
            }
        }
        p{
            &:not(:last-child){
                margin-bottom: 40px;
                @include mobile{
                    margin-bottom: 20px;
                }
            }
            a{
                font-weight: 600;
            }
        }
    }
    #particles-gray-6 {
        position: absolute;
        opacity: 0.15;
        width: 700px;
        height: 700px;
        right: -14%;
        top: 14%;
        transform: rotate(105deg);
        @include tablet{
            display: none;
        }
    }
}