.operational {
  padding: 150px 0;
  @include tablet{
    padding: 50px 0;
  }
  .section-title{
    @include desktop{
      width: 100%;
    }
  }
  &-content {
    &-main {
      width: 526px;
      height: 526px;
      position: relative;
      margin: 0 auto;
      position: relative;
      text-align: center;
      z-index: 1;
      @include desktop{
        width: 400px;
        height: 400px;
      }
      @include mobile{
        width: 300px;
        height: 300px;
      }
      @include mobile-xls{
        width: 250px;
        height: 250px;
      }
      
      img{
        @include desktop{
          width: 100%;
          height: 100%;
        }
      }
      .left-image {
        position: absolute;
        left: -35%;
        bottom: -80px;
        @include desktop{
          height: auto;
          width: 70%;
          bottom: unset;
          top: -45px;
        }
        @include mobile-xs{
          left: -18%;
        }
      }
    }
    &-bg {
      text-align: center;
      min-height: 416px;
      background: #1d4677;
      border-radius: 20px;
      margin-top: -165px;
      position: relative;
      overflow: hidden;
      @include desktop{
        min-height: 325px;
      }
      .button {
        // bottom: -255px;
        margin-top: 255px;
        position: relative;
        @include desktop{
          margin-top: 200px;
        }
      }
      #particles-white {
        position: absolute;
        opacity: 0.15;
        width: 796px;
        height: 796px;
        right: 0;
        top: -20%;
        transform: rotate(-60deg);
      }
    }
    &-obj {
      // position: absolute;
      // top: 0;
      // right: -100%;
      .arrow-one,
      .arrow-two,
      .arrow-three {
        position: absolute;
      }
      .arrow-one {
        right: 20px;
        top: 10%;
        @include desktop{
          display: none;
        }
      }
      .arrow-two {
        right: -10px;
        top: 30%;
        @include desktop{
          display: none;
        }
      }
      .arrow-three {
        right: -15px;
        top: 45%;
        @include desktop{
          display: none;
        }
      }
      .op-one,
      .op-two,
      .op-three {
        position: absolute;
        @include desktop{
          display: none;
        }
      }
      .op-one {
        right: -63%;
        top: -1%;
      }
      .op-two {
        right: -50%;
        top: 20%;
      }
      .op-three {
        right: -62%;
        top: 50%;
      }
    }
  }
}
