.objective {
  // overflow: hidden;
  position: relative;
  @include desktop{
    padding-bottom: 50px;
  }
  @include mobile{
    padding-bottom: 0;
  }
  &-slider {
    &-item {
      max-height: 520px;
      height: 100%;
      margin: 0 15px;
      border-radius: 20px;
      overflow: hidden;
      position: relative;
      &-image {
        height: 100%;
        width: 100%;
        position: relative;
        &::before {
          position: absolute;
          content: "";
          height: 100%;
          width: 100%;
          background: rgba($color: #000000, $alpha: 0.5);
          top: 0;
          left: 0;
          transition: all 0.3s $site-ease;
        }
        img {
          height: 100%;
          width: 100%;
        }
      }
      &-content {
        position: absolute;
        bottom: 60px;
        left: 40px;
        transform: translateY(150px);
        opacity: 0;
        transition: all 0.5s ease;
        visibility: hidden;
        max-width: 550px;
        &::before {
          position: absolute;
          content: "";
          height: 3px;
          width: 100px;
          background: $primary-color;
          bottom: -20px;
          left: 0;
        }
        h2 {
          color: $white;
          font-size: 40px;
          line-height: 50px;
          margin: 0;
          @include mobile-xs{
            font-size: 24px;
            line-height: 30px;
          }
        }
      }
      .objective-slider-progress {
        width: 85%;
        height: 2px;
        position: absolute;
        left: 50%;
        top: 0;
        transform: translateX(-50%);
        opacity: 0;
        .progress {
          width: 0%;
          height: 2px;
          background: $primary-color;
        }
      }
      &:focus {
        outline: 0;
      }
      &.slick-active{
        .objective-slider-item-content{
          transform: translateY(0);
          opacity: 1;
          visibility: visible;
        }
      }
    }
    &-prev,
    &-next {
      height: 150px;
      width: 50px;
      border-radius: 10px;
      position: absolute;
      top: 50%;
      background: $primary-color;
      transform: translateY(-50%);
      z-index: 1;
      cursor: pointer;
      opacity: 0.5;
      transition: all 0.3s ease;
      @include desktop{
        top: unset;
        bottom: -170px;
        transform: translateY(0);
      }
      svg {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
      &:hover {
        opacity: 1;
      }
    }
    &-prev {
      left: 410px;
      @include desktop-lg{
        left: 200px;
      }
      @include desktop{
        left: 44%;
      }
    }
    &-next {
      right: 410px;
      @include desktop-lg{
        right: 200px;
      }
      @include desktop{
        right: 42%;
      }
      svg {
        transform: translate(-50%, -50%) rotate(180deg);
      }
    }
  }
}
.objective-slider .slick-slide,
.objective-slider
  .slick-slide[aria-hidden="true"]:not(.slick-cloned)
  ~ .slick-cloned[aria-hidden="true"] {
  transition: all 0.5s;
  opacity: 0.5;
  .objective-slider-item-content {
    transform: translateY(150px);
    opacity: 0;
    visibility: hidden;
  }
  .objective-slider-progress {
    opacity: 0;
  }
}

.objective-slider .slick-center,
.objective-slider
  .slick-slide[aria-hidden="true"]:not([tabindex="-1"])
  + .slick-cloned[aria-hidden="true"] {
  // transform: scale(1.3);
  z-index: 1;
  opacity: 1;
  .objective-slider-item-content {
    transform: translateY(0);
    opacity: 1;
    visibility: visible;
  }
  .objective-slider-progress {
    opacity: 1;
  }
}
