.single-blog{
    padding: 170px 0 150px 0;
    @include desktop{
        padding: 150px 0 100px 0;
    }
    @include mobile{
        padding: 150px 0 50px 0;
    }
    @include mobile-xs{
        padding: 100px 0 50px 0;
    }
    &-wrapper{
        width: 70%;
        margin: 0 auto;
        @include desktop-lg{
            width: 100%;
        }
        img{
            width: 100%;
            height: 100%;
            min-width: 350px;
            border-radius: 20px;
            margin-bottom: 30px;
        }
        .content{
            width: 60%;
            margin: 0 auto;
            @include desktop-lg{
                width: 80%;
            }
            @include mobile{
                width: 100%;
            }
            p{
                line-height: 31px;
                margin: 0 0 20px 0;
                a{
                    font-weight: bold;
                }
            }
            ul{
                margin: 0 0 30px 30px;
                padding: 0;
                li{
                    font-size: 20px;
                    line-height: 31px;
                    list-style: none;
                    position: relative;
                    @include mobile{
                        font-size: 16px;
                        line-height: 26px;
                    }
                    &::before{
                        position: absolute;
                        content: '';
                        height: 5px;
                        width: 5px;
                        background: $text-color-dark;
                        top: 16px;
                        transform: translateY(-50%) translateX(-15px);
                    }
                }
            }
        }
    }
}