.about-banner {
    min-height: 800px;
    display: flex;
    align-items: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    border-radius: 0 0 50px 50px;
    padding: 220px 0 130px;
    position: relative;
    z-index: 1;

    @include mobile-xs{
        padding: 90px 0 80px;
    }
    @include tablet{
        padding: 140px 0 80px;
    }
    @include desktop{
        border-radius: 0 0 20px 20px;
    }
    &-wrapper{
        display: grid;
        grid-template-columns: 60% 40%;
        grid-column-gap: 30px;
        align-items: center;
        margin-left: 100px;
        @include desktop-xl{
            margin-left: 50px;
            margin-right: 50px;
        }
        @include desktop-lg{
            grid-template-columns: 50% 50%;
        }
        @include desktop{
            margin-left: 0;
            margin-right: 0;
        }
        @include tablet{
            grid-template-columns: 100%;
        }
        &-content {
            @include tablet{
                margin-bottom: 50px;
                text-align: center;
            }
            h1,
            p {
                color: $white;
            }
            h1 {
                font-size: 30px;
                margin-bottom: 18px;
                line-height: 40px;
            }
            p {
                line-height: 30px;
                &:not(:last-child){
                    margin-bottom: 35px;
                    @include desktop-lg{
                        margin-bottom: 20px;
                    }
                }
            }
        }
        &-image {
            margin: 0 0 -200px 0;
            border-radius: 20px;
            position: relative;
            z-index: 2;
            @include desktop-lg{
                margin: 0;
            }
            &::before{
                position: absolute;
                content: '';
                height: 100px;
                width: 90%;
                border-radius: 20px;
                top: -30px;
                left: 50%;
                transform: translateX(-50%);
                background: $primary-color;
                z-index: -1;
                @include tablet{
                    width: 80%;
                    top: -20px;
                }
            }
            img{
                border-radius: 20px;
                height: 100%;
                width: 100%;
            }
        }
    }
    .about-triangles{
        width: 100%;
        height: 100%;
        position: absolute;
        right: 0;
        top: -10%;
        z-index: -1;
        @include mobile{
        width: 90%;
        right: 10%;
        }
        img {
        transform-origin: center center;
        &:nth-child(1) {
            position: absolute;
            top: 40%;
            left: 6%;
            animation-name: opacity-animation;
            animation-duration: 2.5s;
            animation-iteration-count: infinite;
            // animation-direction: alternate;
        }
        &:nth-child(2) {
            position: absolute;
            top: 27%;
            right: 6%;
            width: 21px;
            transform: rotate(10deg);
            animation-name: opacity-animation;
            animation-duration: 2.5s;
            animation-delay: -1s;
            animation-iteration-count: infinite;
        }
        &:nth-child(3) {
            position: absolute;
            bottom: 1%;
            left: 10%;
            width: 10px;
            transform: rotate(2deg);
            animation-name: opacity-animation;
            animation-duration: 2.5s;
            animation-delay: -1.5s;
            animation-iteration-count: infinite;
        }
        &:nth-child(4) {
            position: absolute;
            bottom: 0%;
            right: 7%;
            transform: rotate(10deg);
            animation-name: opacity-animation;
            animation-duration: 2s;
            animation-delay: -0.5s;
            animation-iteration-count: infinite;
        }
        }
    }
}
  