.team {
  &.awesome {
    padding: 150px 0 210px 0;
    position: relative;
    overflow: hidden;
    @include tablet{
      padding: 50px 0;
    }
    .team-wrapper {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-column-gap: 100px;
      align-items: center;
      @include desktop{
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 30px;
      }
      .team-grid {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 30px;
        align-items: end;
        @include desktop{
          height: 100%;
          width: 100%;
        }
        @include mobile{
          grid-template-columns: repeat(1, 1fr);
        }
        &-item {
          justify-self: end;
          position: relative;
          @include desktop{
            height: 100%;
            width: 100%;
          }
          &-image {
            overflow: hidden;
            border-radius: 20px;
            display: flex;
            @include desktop{
              height: 100%;
              width: 100%;
            }
            img{
              @include desktop{
                height: 100%;
                width: 100%;
                object-fit: cover;
              }
            }
          }
          &-content {
            position: absolute;
            background: $white;
            text-align: center;
            padding: 15px 28px;
            border-radius: 20px 20px 20px 0;
            top: 25px;
            right: 0;
            transform: translateX(50%) scale(0);
            transform-origin: left bottom;
            z-index: 2;
            box-shadow: 0 20px 40px rgba($color: #000000, $alpha: 0.1);
            transition: transform 0.3s $site-ease;
            min-width: 140px;
            @include desktop{
              transform: translate(0, 0) scale(1);
              top: unset;
              bottom: 30px;
              right: unset;
              left: 30px;
              border-radius: 20px;
            }
            h5 {
              font-size: 20px;
              font-weight: 600;
              line-height: 30px;
            }
            p {
              font-size: 14px;
              margin: 0;
            }
          }
          &:first-child {
            transform: translateY(60px);
            z-index: 2;
            @include desktop{
              transform: translateY(0);
            }
          }
          &:nth-child(3) {
            transform: translate(43px, 60px);
            z-index: 3;
            @include desktop{
              transform: translate(0, 0);
            }
          }
          &:nth-child(4) {
            z-index: 2;
            align-self: start;
          }
          &:hover {
            .team-grid-item-content {
              transform: translateX(50%) scale(1);
              @include desktop{
                transform: translate(0, 0);
              }
            }
          }
        }
      }
      .team-content {
        @include desktop{
          grid-row-start: 1;
          text-align: center;
        }
        h2 {
          font-size: 50px;
          line-height: 60px;
          margin: 0 0 50px 0;
          position: relative;
          @include mobile{
            font-size: 30px;
            line-height: 40px;
          }
          &::before {
            position: absolute;
            content: "";
            height: 3px;
            width: 100px;
            left: 0;
            bottom: -24px;
            background: $text-color-dark;
            @include desktop{
              left: 50%;
              transform: translateX(-50%);
            }
          }
        }
        p {
          margin: 0 0 60px 0;
          line-height: 31px;
          @include mobile{
            line-height: 26px;
            margin: 0 0 30px 0;
          }
        }
      }
    }
    #particles-gray-1 {
      position: absolute;
      opacity: 0.1;
      width: 796px;
      height: 796px;
      right: -10%;
      bottom: -20%;
      transform: rotate(-60deg);
    }
  }
  &.leadership,
  &.business {
    padding: 150px 0;
    position: relative;
    overflow: hidden;
    @include desktop {
      padding: 100px 0;
    }
    @include tablet {
      padding: 50px 0;
    }
    .team-wrapper {
      margin: 0 100px;
      @include desktop {
        margin: 0;
      }
      .team-content {
        h2 {
          font-size: 30px;
          line-height: 40px;
          margin: 0 0 60px 0;
          position: relative;
          &::before {
            position: absolute;
            content: "";
            left: 0;
            bottom: -17px;
            width: 100px;
            height: 3px;
          }
        }
      }
      .team-grid {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 30px;
        @include desktop {
          grid-template-columns: repeat(2, 1fr);
        }
        @include mobile {
          display: block;
        }
        &-item {
          @include mobile {
            margin-bottom: 30px;
          }
          &-image {
            max-height: 350px;
            height: 100%;
            width: 100%;
            overflow: hidden;
            border-radius: 20px;
            display: flex;
            @include tablet {
              max-height: 350px;
              min-height: 350px;
              display: grid;
            }
            @include mobile {
              max-height: 450px;
              min-height: 450px;
              display: grid;
            }
            img {
              width: 100%;
              height: 100%;
              object-fit: cover;
            }
          }
          &-content {
            padding: 20px 20px 0 0;
            h5 {
              font-size: 20px;
              line-height: 30px;
              margin-bottom: 5px;
            }
            span {
              display: block;
              font-size: 14px;
              line-height: 21px;
              margin: 0 0 10px 0;
            }
            p {
              font-size: 16px;
              line-height: 23px;
              margin: 0;
              width: 90%;
            }
          }
        }
      }
    }
    &.dark {
      background: $text-color-dark;
      border-radius: 50px;
      @include desktop{
        border-radius: 20px;
      }
      #particles-white-2{
        position: absolute;
        opacity: 0.15;
        width: 796px;
        height: 796px;
        right: -22%;
        top: -35%;
        transform: rotate(-60deg);
        @include desktop{
          display: none;
        }
      }
      .team-wrapper {
        .team-content {
          h2 {
            color: $white;
            &::before {
              background: $white;
            }
          }
        }
        .team-grid {
          &-item {
            &-content {
              h5,
              span,
              p {
                color: $white;
              }
            }
          }
        }
      }
    }
    &.light {
      background: $white;
      #particles-gray-8{
        position: absolute;
        opacity: 0.15;
        width: 796px;
        height: 796px;
        right: -18%;
        bottom: -50%;
        transform: rotate(-60deg);
        @include desktop{
          display: none;
        }
      }
      .team-wrapper {
        .team-content {
          h2 {
            margin-right: 48%;
            @include desktop{
              margin-right: 0;
            }
            &::before {
              background: $text-color-dark;
            }
          }
        }
      }
    }
  }
}
