.project-info{
    margin-top: -135px;
    position: relative;
    overflow: hidden;
    &-wrapper{
        background: $text-color-dark;
        padding: 30px 190px 30px 100px;
        border-radius: 20px;
        display: grid;
        grid-template-columns: 215px auto;
        align-items: center;
        column-gap: 50px;
        @include desktop-lg{
            padding: 30px 100px 30px 100px;
        }
        @include desktop{
            padding: 30px 50px 30px 50px;
        }
        @include tablet{
            grid-template-columns: auto;
            row-gap: 20px;
            text-align: center;
        }
        #particles-white-1 {
            position: absolute;
            opacity: 0.15;
            width: 796px;
            height: 796px;
            left: 9%;
            top: -20%;
            transform: rotate(-60deg);
            @include mobile {
                opacity: .075;
            }
        }
        &-image{
            height: 215px;
            width: 215px;
            @include tablet{
                height: 150px;
                width: 150px;
                margin: 0 auto;
            }
            img{
                height: 100%;
                width: 100%;
            }
        }
        &-content{
            h3{
                color: $white;
                line-height: 53px;
                margin: 0;
                @include mobile{
                    font-size: 30px;
                    line-height: 40px;
                }
            }
        }
    }
    .progect-info-triangles{
        width: 70%;
        height: 100%;
        position: absolute;
        left: 14%;
        top: 0;
        @include mobile{
        width: 90%;
        right: 10%;
        }
        img {
        transform-origin: center center;
        &:nth-child(1) {
            position: absolute;
            top: 40%;
            left: 3%;
            animation-name: opacity-animation;
            animation-duration: 2.5s;
            animation-iteration-count: infinite;
            // animation-direction: alternate;
        }
        &:nth-child(2) {
            position: absolute;
            bottom: 14%;
            right: 0;
            width: 21px;
            transform: rotate(10deg);
            animation-name: opacity-animation;
            animation-duration: 2.5s;
            animation-delay: -1s;
            animation-iteration-count: infinite;
        }
        &:nth-child(3) {
            position: absolute;
            top: 15%;
            right: 10%;
            width: 10px;
            transform: rotate(2deg);
            animation-name: opacity-animation;
            animation-duration: 2.5s;
            animation-delay: -1.5s;
            animation-iteration-count: infinite;
        }
        }
    }
}