.client{
    padding-bottom: 150px;
    @include tablet{
        padding-bottom: 50px;
    }
    .section-title{
        @include tablet{
            width: 100%;
        }
    }
    &-wrapper{
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        align-items: center;
        column-gap: 30px;
        text-align: center;
        @include desktop{
            grid-template-columns: repeat(2, 1fr);
            row-gap: 30px;
        }
        @include tablet{
            grid-template-columns: repeat(1, 1fr);
        }
        &-item{
            border: 1px solid #E0E2E9;
            border-radius: 20px;
            align-self: center;
            min-height: 150px;
            align-items: center;
            display: flex;
            justify-content: center;
        }
    }
}