.footer {
  padding: 50px 0 70px 0;
  background: $text-color-dark;
  border-radius: 50px 50px 0 0;
  @include desktop{
    border-radius: 20px 20px 0 0;
  }
  &-logo {
    text-align: center;
    margin-bottom: 40px;
  }
  &-widgets {
    display: grid;
    grid-template-columns: auto auto auto auto;
    column-gap: 50px;
    padding: 0 100px;
    @include desktop-lg{
      padding: 0;
    }
    @include desktop{
      grid-template-columns: auto auto;
      row-gap: 50px;
    }
    @include tablet{
      grid-template-columns: auto;
      row-gap: 30px;
      text-align: center;
    }
    .footer-widget {
      h4 {
        font-size: 15px;
        text-transform: uppercase;
        color: rgba($color: $white, $alpha: 0.6);
        margin-bottom: 23px;
      }
      p {
        color: rgba($color: $white, $alpha: 0.35);
        font-size: 15px;
        line-height: 23px;
      }
    }
    .importants-links {
      padding-right: 30px;
      @include desktop{
        padding-right: 0;
      }
      ul {
        margin: 0;
        padding: 0;
        li {
          list-style: none;
          a {
            padding: 5px 0;
            color: rgba($color: $white, $alpha: 0.6);
            text-decoration: none;
            transition: all 0.3s ease;
            display: block;
            line-height: 1;
          }
        }
      }
    }
    .subscription {
      form {
        .email {
          width: 300px;
          min-width: 300px;
          height: 50px;
          display: block;
          margin-bottom: 20px;
          border: 1px solid #6b7989;
          background: transparent;
          color: rgba($color: $white, $alpha: 0.6);
          border-radius: 10px;
          padding: 0 14px;
          font-size: 15px;
          font-family: $primary-font;
          @include desktop{
            width: 100%;
            min-width: 100%;
          }
          &:focus {
            outline: 0;
          }
        }
        ::placeholder {
          color: rgba($color: $white, $alpha: 0.6);
          font-family: $primary-font;
        }
        .button {
          width: 100%;
          border: none;
          font-family: $primary-font;
        }
      }
    }
  }
}
